body {
    background-color: rgb(223, 235, 54);
  }

  button {
      background-color: #fff;
      border: 2px solid black;
  }

  input{
    border: 2px solid black;  
  }

  div {
      margin: 2px;
  }

  p {
    color: grey;
    padding-top: 3px;
    font-size: 15px;
  }

  .message {
    color: green;
    background: lightgreen;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }

  .error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }